<template>
    <!---->  
    <div class="adminMainContent">
        <!--loading-->
        <loading 
          :active.sync="isLoading"
          color="#646159"
          :height="loadingScale"
          :width="loadingScale"
        ></loading>   
        
        <div class="adminMainContent-Top">
            <span class="adminBreadcrumbs"></span>
            <button class="adminBtn" @click="openModal(true)"></button>
        </div>
        <table class="tableClass">
            <thead class="tableHead">
                <tr class="mt-4">
                    <th class="tableHead_th mt-4"></th>
                    <th class="tableHead_th" width="100"></th>
                    <th class="tableHead_th" width="100"></th>
                    <th class="tableHead_th" width="100"></th>
                    <th class="tableHead_th" width="100"></th>
                </tr>
            </thead>
            <tbody class="tableBody">
                <tr v-for="coupon in coupons" :key="coupon.due_date" class="tableRow mt-4">
                    <td>{{coupon.title}}</td>
                    <td>{{coupon.percent}}</td>
                      <td>{{coupon.code}}</td>
                    <td>
                        <span v-if="coupon.is_enabled" class="text-success"></span>
                        <span v-else></span>
                    </td>
                    <td>
                        <button class="btn btn-outline-primary btn-sm" @click="openModal(false, coupon)"></button>
                        <button class="btn btn-outline-danger btn-sm" @click="openDeleteModal(coupon)"></button>
                    </td>

                </tr>
            </tbody>
        </table>

        <!--pagination-->
        <Pagination @updatepage="getCoupons" :pages="getPagination"></Pagination>

        <!--/Modal-->  
        <div class="modal fade" id="couponModal" tabindex="-1" role="dialog"
          aria-labelledby="exampleModalLabel" aria-hidden="true">
          <div class="modal-dialog modal-md" role="document">
              <div class="modal-content border-0">
                  <div class="modal-header  bg-secondary text-white modalHeader">
                      <p class="modal-title" id="exampleModalLabel">
                          
                      </p>
                      <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                          <img :src="crossImg" style="width:50%">
                      </button>
                  </div>
                  <div class="modal-body">
                      <div class="modal_right">
                          <div>
                              <label for="title"></label>
                              <input type="text" class="modalInput" id="title" placeholder="" v-model="tempCoupon.title">
                          </div>
                          <div>
                              <label for="code"></label>
                              <input type="text" class="modalInput" id="code" placeholder="" v-model="tempCoupon.code">
                          </div>
                          <div>
                              <label for="percent"></label>
                              <input type="text" class="modalInput" id="percent" placeholder="(ex:85)" v-model="tempCoupon.percent">
                          </div>
                          <div>
                              <div>
                                  <input type="checkbox" id="is_enabled" v-model="tempCoupon.is_enabled" :true-value="1" :false-value="0">
                                  <label class="form-check-label" for="is_enabled">
                                  
                                  </label>
                              </div>
                          </div>
                          <div class="modal_footer">
                              <div class="modal_footerBtn">
                                  <button type="button" class="btn btn-secondary" @click="updateCoupon"></button>
                              </div>
                              <div class="modal_footerBtn">
                                  <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
                              </div>
                          </div>
                      </div>
                  </div>
              </div>
          </div>
      </div>


    <!--Modal-->
    <div class="modal fade" id="delCouponModal" tabindex="-1" role="dialog"
        aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div class="modal-dialog" role="document">
            <div class="modal-content border-0">
            <div class="modal-header bg-danger text-white modalHeader">
                <p class="modal-title"></p>
                <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                  <img :src="crossImg" style="width:50%">
                </button>
            </div>
            <div class="modal-body">
                 <strong class="text-danger">{{tempCoupon.title}}</strong> ()
            </div>
            <div class="modal-footer">
                <button type="button" class="btn btn-danger" @click="deleteCoupon"></button>
                <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            </div>
            </div>
        </div>
    </div>


  </div>
</template>

<script>
import $ from 'jquery';
import Pagination from '@/components/Pagination';

export default {
  name: 'AdminCoupon',
  components: {
    Pagination,
  },
  data(){
    return{
      crossImg: require("@/assets/img/modal/cross.png"),
      coupons: [],
      tempCoupon: {},
      isNew: false,
      isLoading: false,
      getPagination: {},
      loadingScale: 50,
    }
  },
  methods:{
    //
    getCoupons( page=1 ){
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupons?page=${page}`;
      const vm = this;
      vm.isLoading = true;
      this.$http.get(api).then((res)=>{
        vm.isLoading = false;
        vm.getPagination = res.data.pagination;
        //console.log('',res.data);
        vm.coupons = res.data.coupons;
        //console.log('',vm.coupons);
      })
    },
    //&
    updateCoupon(){
      let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon`;
      let httpMethod = 'post';
      const vm = this;
      if(!vm.isNew){
        api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon/${vm.tempCoupon.id}`;
        httpMethod = 'put';
      }
      //console.log('temp',this.tempCoupon);
      this.$http[httpMethod](api, {data: vm.tempCoupon}).then((res)=>{
        //console.log('',res.data);
        if(res.data.success){
          $('#couponModal').modal('hide');
          vm.getCoupons();
        }else{
          $('#couponModal').modal('hide');
          vm.getCoupons();
          //console.log('');
        }
      })
    },
    //
    deleteCoupon(){
      //console.log(this.tempCoupon);
      const vm = this;
      let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon/${vm.tempCoupon.id}`;
      vm.isLoading = true;
      $('#delCouponModal').modal('hide');
      this.$http.delete(api).then((res)=>{
        vm.isLoading = false;
        //console.log('',res.data);
        vm.getCoupons();
      })
    },


    //&
    openModal(isNew, item){
      if(isNew){
        this.tempCoupon = {};
        this.isNew = true;
        //console.log('',this.tempProduct);
      }else{
        this.tempCoupon = Object.assign({}, item);
        this.isNew = false;
        //console.log('',this.tempProduct);
      }
      $('#couponModal').modal('show');
    },
    //
    openDeleteModal(item){
      this.tempCoupon = Object.assign({}, item);
      $('#delCouponModal').modal('show');
    },
  },
  created(){
    this.getCoupons();
  }
}
</script>




