<template>
    <!--Product List-->  
    <div class="adminMainContent">
        <!--loading-->
        <loading 
          :active.sync="isLoading"
          color="#646159"
          :height="loadingScale"
          :width="loadingScale"
        ></loading>   
        <div class="adminMainContent-Top">
            <span class="adminBreadcrumbs">Product List</span>
            <button class="adminBtn" @click="openModal(true)">Create Product</button>
        </div>
        <table class="tableClass">
            <thead class="tableHead">
                <tr class="mt-4">
                    <th class="tableHead_th mt-4" width="100">Categories</th>
                    <th class="tableHead_th">Product Name</th>
                    <th class="tableHead_th" width="120">Original Price</th>
                    <th class="tableHead_th" width="120">Price</th>
                    <th class="tableHead_th" width="100">Enable</th>
                    <th class="tableHead_th" width="150">Edit</th>
                </tr>
            </thead>
            <tbody class="tableBody">
                <tr v-for="product in products" :key="product.id" class="tableRow mt-4">
                    <td>{{product.category}}</td>
                    <td>{{product.title}}</td>
                    <td>
                        {{product.origin_price|currency}}
                    </td>
                    <td>
                        {{product.price|currency}}
                    </td>
                    <td>
                        <span v-if="product.is_enabled" class="text-success">Enabled</span>
                        <span v-else>Disable</span>
                    </td>
                    <td>
                        <button class="btn btn-outline-primary btn-sm" @click="openModal(false, product)">Edit</button>
                        <button class="btn btn-outline-danger btn-sm" @click="openDeleteModal(product)">Delete</button>
                    </td>

                </tr>
            </tbody>
        </table>

        <!--pagination-->
        <Pagination @updatepage="getProducts" :pages="getPagination"></Pagination>

        <!--/Modal-->  
        <div class="modal fade" id="productModal" tabindex="-1" role="dialog"
          aria-labelledby="exampleModalLabel" aria-hidden="true">
          <div class="modal-dialog modal-lg" role="document">
              <div class="modal-content border-0">
                  <div class="modal-header  bg-secondary text-white modalHeader">
                      <p class="modal-title" id="exampleModalLabel"></p>
                      <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                          <img :src="crossImg" style="width:50%">
                      </button>
                  </div>
                  <div class="modal-body">
                      <div class="modal_left">
                          <div>
                              <label for="image"></label>
                              <input type="text" class="modalInput" id="image" placeholder="" v-model="tempProduct.imageUrl">
                          </div>
                          <div>
                              <label for="customFile">
                                  
                                  <i class="fas fa-spinner fa-pulse" v-if="fileUploading"></i>
                              </label>
                              <input type="file" id="customFile" class="modalInput" ref="files" @change="uploadFile">
                          </div>
                          <img :src="tempProduct.imageUrl" class="img-fluid" alt="">
                      </div>
                      <div class="modal_right">
                          <div>
                              <label for="title"></label>
                              <input type="text" class="modalInput" id="title" placeholder="" v-model="tempProduct.title">
                          </div>
                          <div class="modal_row">
                              <div class="modal_inputAria">
                                  <label for="category"></label>
                                  <input type="text" class="modalInput" id="category" placeholder="" v-model="tempProduct.category">
                              </div>
                              <div class="modal_inputAria">
                                  <label for="unit"></label>
                                  <input type="unit" class="modalInput" id="unit" placeholder="" v-model="tempProduct.unit">
                              </div>
                          </div>
                          <div class="modal_row">
                              <div class="modal_inputAria">
                                  <label for="origin_price"></label>
                                  <input type="text" class="modalInput" id="origin_price" placeholder="" v-model="tempProduct.origin_price">
                              </div>
                              <div class="modal_inputAria">
                                  <label for="price"></label>
                                  <input type="text" class="modalInput" id="price" placeholder="" v-model="tempProduct.price">
                              </div>
                          </div>
                          <div class="modal_row">
                              <div class="modal_inputAria">
                                  <label for="size"></label>
                                  <input type="text" class="modalInput" id="size" placeholder="" v-model="tempProduct.size">
                              </div>
                              <div class="modal_inputAria">
                                  <label for="ingredient">/</label>
                                  <input type="text" class="modalInput" id="ingredient" placeholder="" v-model="tempProduct.ingredient">
                              </div>
                          </div>
                          <div>
                              <div>
                                  <input type="checkbox" id="is_enabled" v-model="tempProduct.is_enabled" :true-value="1" :false-value="0">
                                  <label class="form-check-label" for="is_enabled">
                                  
                                  </label>
                              </div>
                          </div>
                          <div class="modal_footer">
                              <div class="modal_footerBtn">
                                  <button type="button" class="btn btn-secondary" @click="updateProduct"></button>
                              </div>
                              <div class="modal_footerBtn">
                                  <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
                              </div>
                          </div>
                      </div>
                  </div>
              </div>
          </div>
      </div>


    <!--Modal-->
    <div class="modal fade" id="delProductModal" tabindex="-1" role="dialog"
        aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div class="modal-dialog" role="document">
            <div class="modal-content border-0">
            <div class="modal-header bg-danger text-white modalHeader">
                <p class="modal-title"></p>
                <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                    <img :src="crossImg" style="width:50%">
                </button>
            </div>
            <div class="modal-body">
                 <strong class="text-danger">{{ tempProduct.title }}</strong> ()
            </div>
            <div class="modal-footer">
                <button type="button" class="btn btn-danger" @click="deleteProduct"></button>
                <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            </div>
            </div>
        </div>
    </div>


  </div>
</template>

<script>
import $ from 'jquery';
import Pagination from '@/components/Pagination';

export default {
  name: 'AdminProduct',
  components: {
    Pagination,
  },
  data(){
    return{
      crossImg: require("@/assets/img/modal/cross.png"),
      products: [],
      tempProduct: {},
      isNew: false,
      isLoading: false,
      fileUploading: false,
      pagination: {},
      getPagination: {},
      loadingScale: 50,

    }
  },
  methods:{
    //
    getProducts( page=1 ){
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/products?page=${page}`;
      const vm = this;
      vm.isLoading = true;
      this.$http.get(api).then((res)=>{
        vm.isLoading = false;
        //vm.pagination = res.data.pagination;
        vm.getPagination = res.data.pagination;
        //console.log('',res.data);
        vm.products = res.data.products;
        //console.log('',vm.products);
      })
    },
    //&
    updateProduct(){
      let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product`;
      let httpMethod = 'post';
      const vm = this;
      if(!vm.isNew){
        api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product/${vm.tempProduct.id}`;
        httpMethod = 'put';
      }
      console.log('temp',this.tempProduct)
      this.$http[httpMethod](api, {data: vm.tempProduct}).then((res)=>{
        //console.log('',res.data);
        if(res.data.success){
          $('#productModal').modal('hide');
          vm.getProducts();
        }else{
          $('#productModal').modal('hide');
          vm.getProducts();
          //console.log('');
        }
      })
    },
    //
    deleteProduct(){
      //console.log(this.tempProduct);
      const vm = this;
      let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product/${vm.tempProduct.id}`;
      vm.isLoading = true;
      $('#delProductModal').modal('hide');
      this.$http.delete(api).then((res)=>{
        vm.isLoading = false;
        vm.getProducts();
        //console.log('',res.data);
      })
    },


    //
    openModal(isNew, item){
      if(isNew){
        this.tempProduct = {};
        this.isNew = true;
        //console.log('',this.tempProduct);
      }else{
        this.tempProduct = Object.assign({}, item);
        this.isNew = false;
        //console.log('',this.tempProduct);
      }
      $('#productModal').modal('show');
    },
    openDeleteModal(item){
      this.tempProduct = Object.assign({}, item);
      $('#delProductModal').modal('show');
    },

    //
    uploadFile(){
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/upload`;
      vm.fileUploading = true;
      const uploadedFile = this.$refs.files.files[0];
      const formData = new FormData();
      formData.append('file-to-upload', uploadedFile);
      this.$http.post(api, formData,{
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      }).then((res)=>{
        vm.fileUploading = false;
        //console.log(res.data);
        if(res.data.success){
          vm.$set(vm.tempProduct, 'imageUrl', res.data.imageUrl);
        }else{
          vm.$bus.$emit('message:push', res.data.message, 'secondary');
        }
      });
    }
  },
  created(){
    this.getProducts();
  }
}
</script>




